import { Component, Event, EventEmitter, h, Host, Prop } from '@stencil/core';
import { createColorClasses } from '../../../../utils/functions/color.function';
import { TpColor } from '../../../../utils/types/color.type';

@Component({
  tag: 'tp-check-card',
  styleUrl: 'check-card.scss',
  scoped: true,
})
export class TpCheckCard {
  /**
   * todo
   */
  @Prop({ reflect: true }) color?: TpColor;

  /**
   * todo
   */
  @Prop({ reflect: true }) alert = false;

  /**
   * todo
   */
  @Prop({ reflect: true }) titulo?: string;

  /**
   * todo
   */
  @Prop({ reflect: true }) categoria?: string;

  /**
   * todo
   */
  @Prop({ reflect: true }) horaInicial?: string;

  /**
   * todo
   */
  @Prop({ reflect: true }) horaFinal?: string;

  /**
   * todo
   */
  @Prop({ reflect: true }) dataInicial?: string;

  /**
   * todo
   */
  @Prop({ reflect: true }) dataFinal?: string;

  /**
   * todo
   */
  @Prop({ reflect: true }) finalizada?: string;

  /**
   * todo
   */
  @Prop({ reflect: true }) iconName?: string;

  /**
   * todo
   */
  @Event() tpClick!: EventEmitter;

  /**
   * todo
   */
  @Event() tpTooltipClose!: EventEmitter;

  onClick() {
    this.tpClick.emit();
  }

  onTooltipCloseClick() {
    this.tpTooltipClose.emit();
  }

  render() {
    const {
      color,
      alert,
      titulo,
      categoria,
      horaInicial,
      horaFinal,
      dataInicial,
      dataFinal,
      iconName,
    } = this;

    return (
      <Host
        from-stencil
        class={createColorClasses(color, {
          'tp-check-card': true,
          'tp-check-card--alert': alert,
        })}
      >
        <ion-item
          tp-spacingx="s00"
          tp-spacingy="s00"
          class="tp-check-card__base"
        >
          <div slot="start" class="tp-check-card__input-container">
            <slot name="input"></slot>
          </div>

          <div class="tp-check-card__container" onClick={() => this.onClick()}>
            <div class="tp-check-card__text-container">
              <ion-text class="tp-check-card__title" tp-type="p16xb">
                <h1>{titulo}</h1>
              </ion-text>

              <div class="tp-check-card__title-wrap">
                <div class="tp-check-card__info-container">
                  <div class="tp-check-card__container-fix">
                    <ion-icon
                      class="tp-check-card__icon"
                      src={iconName}
                    ></ion-icon>

                    <ion-text class="tp-check-card__subtitulo" tp-type="p12xb">
                      <span>{categoria}</span>
                    </ion-text>
                  </div>

                  {!dataInicial && !dataFinal && horaInicial && horaFinal && (
                    <ion-text class="tp-check-card__hora" tp-type="p12x">
                      <span>
                        {horaInicial} – {horaFinal}
                      </span>
                    </ion-text>
                  )}

                  {dataInicial && dataFinal && !horaInicial && !horaFinal && (
                    <ion-text class="tp-check-card__data" tp-type="p12x">
                      <span>
                        {dataInicial} até {dataFinal}
                      </span>
                    </ion-text>
                  )}
                </div>
              </div>

              {dataInicial && dataFinal && horaInicial && horaFinal && (
                <ion-text class="tp-check-card__data-hora" tp-type="p12x">
                  <span>
                    {dataInicial} - {horaInicial} até {dataFinal} - {horaFinal}
                  </span>
                </ion-text>
              )}
            </div>
          </div>
        </ion-item>

        <div class="tp-check-card__tooltip-container">
          <slot name="tooltip"></slot>
        </div>
      </Host>
    );
  }
}
