:host {
  --tp-background: var(--tp-color-neutral-2);

  display: flex;
  align-items: center;
  max-width: 366px;
  border-radius: 8px;
  justify-content: space-between;
  border: 1px solid transparent;
  background: var(--tp-background);

  [slot="tooltip"] {
    padding: 12px 12px 12px 0;
  }
}

.tp-check-card {
  &__title {
    margin-right: 16px;

    &-wrap {
      display: flex;
      margin-top: 4px;
    }
  }

  &__base {
    --background: transparent;
    display: flex;
    align-items: center;

    [slot="start"] {
      margin-inline-end: 0;
    }

    &:hover {
      --background-hover: transparent;
    }
  }

  &__container {
    padding: 12px 0 12px 0;
    display: flex;
    align-items: center;
    cursor: pointer;
    flex: 1;
  }

  &__input-container {
    padding: 12px 0 12px 16px;
  }

  &__text-container {
    padding: 0 18px;
    flex: 1;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
  }

  &__info-container {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }

  &__icon {
    color: var(--tp-color-neutral-8);
    width: 16px;
    height: 16px;
  }

  &__container-fix {
    display: flex;
    align-items: center;
  }

  &__subtitulo {
    color: var(--tp-color-neutral-8);
    margin: 0 8px;
  }

  &__hora {
    color: var(--tp-color-neutral-8);
  }

  &__data {
    color: var(--tp-color-neutral-8);
  }

  &__data-hora {
    margin-top: 8px;
    color: var(--tp-color-neutral-8);
  }

  &__tooltip-container {
    display: none;

    ::slotted(tp-tooltip) {
      ion-button {
        color: var(--tp-color-warning);
      }
    }
  }
}

//
// state
//

:host(.tp-check-card--alert) {
  border: 1px solid var(--tp-color-warning);

  .tp-check-card__tooltip-container {
    display: block;
    padding: 12px 16px 12px 0;
  }
}

//
// color
//

:host(.tp-color) {
  --tp-background: var(--tp-color-base);

  ::slotted(ion-checkbox) {
    --border-color: var(--tp-color-contrast);
    --border-color-checked: var(--tp-color-contrast);
    --checkbox-background-checked: var(--tp-color-contrast);
    --checkmark-color: var(--tp-color-base);

    &:hover {
      --border-color-checked: var(--tp-color-contrast);
      --checkbox-background-checked: var(--tp-color-contrast);
    }
  }

  .tp-check-card {
    &__title {
      color: var(--tp-color-contrast);
    }

    &__icon {
      color: var(--tp-color-contrast);
    }

    &__subtitulo {
      color: var(--tp-color-contrast);
    }

    &__hora {
      color: var(--tp-color-contrast);
    }

    &__data {
      color: var(--tp-color-contrast);
    }

    &__data-hora {
      color: var(--tp-color-contrast);
    }
  }
}